<template>
    <da-dialog :title="title" size="850px" :visible.sync="dialogVisible">
        <div class="target-dialog-flex">
            <da-tree v-model="treeValue" class="target-tree-wrap" :data-lists="treeList" :expand-on-click-node="false" :default-expand="true" @currentChange="treeChange">
            </da-tree>
            <div class="target-radio-warp">
                <el-radio-group v-model="targetId" v-loading="isLoading ===1" @change="changeHandle">
                    <el-radio class="target-radio-item" v-for="item in targetList" :key="item.id" :label="item">{{item.name}}</el-radio>
                </el-radio-group>
                <p v-if="!targetList||targetList.length===0">
                    请选择其他部门
                </p>
            </div>
            <da-calendar ref="tragetCalendar" class="target-calendar-warp" :target-id="targetId" :disabled="!targetId" v-loading="isLoading ===2" v-model="calendarValue" @change="changeHandle"></da-calendar>
        </div>
        <div slot="footer" class="dialog-footer">
            <el-button @click="saveData()">保存</el-button>
            <el-button @click="dialogVisible=false">关闭</el-button>
        </div>
    </da-dialog>
</template>
<script>
import daCalendar from "@/components/base/calendar.vue";
export default {
  components: {
    daCalendar
  },
  props: {
    title: {
      type: String,
      default: ""
    },
    visible: {
      type: Boolean,
      default: false
    },
    dataList: {
      type: Array,
      default() {
        return [];
      }
    }
  },
  data() {
    return {
      dialogVisible: false,
      isLoading: false,
      treeValue: "",
      targetId: "",
      treeList: [],
      targetList: [],
      calendarValue: [],
      calendarData: []
    };
  },
  watch: {
    //   XXX 解决弹窗的sync显示隐藏问题
    dialogVisible(val) {
      if (val !== this.visible) {
        this.$emit("update:visible", val);
      }
      if (this.dialogVisible === true) {
        this.$nextTick(() => {
          this.treeList = this.dataList;
        });
      } else {
        this.treeList = [];
      }
    },
    visible(val) {
      if (val !== this.dialogVisible) {
        this.dialogVisible = val;
      }
    }
  },
  methods: {
    treeChange(node) {
      this.isLoading = 1;
      this.$ajax
        .get("rest/targetData/orgTarget", {
          params: {
            orgId: node.obj.id
          },
          noLoading: true
        })
        .then(res => {
          this.isLoading = false;
          this.targetList = res.data.records;
          this.targetId = "";
        });
    },
    changeHandle() {
      this.isLoading = 2;
      this.$ajax
        .get("rest/targetData/historyData", {
          params: {
            targetId: this.targetId.id,
            startTime: this.calendarValue[0] + " 00:00:00",
            endTime: this.calendarValue[1] + " 23:59:59"
          },
          noLoading: true
        })
        .then(res => {
          this.isLoading = false;
          this.calendarData = res.data.records;
          this.$refs.tragetCalendar.setInputs(
            res.data.records,
            "dataValue",
            "dataTime"
          );
        });
    },
    saveData() {
      let dataLists = this.$refs.tragetCalendar.getInputs().filter(item => {
        return item.id || item.dataValue;
      });
      this.isLoading = 2;
      this.$ajax.put("rest/targetData/data/batch", dataLists).then(res => {
        this.isLoading = false;
        if (res.data.status) {
          this.changeHandle();
          this.$message.success("保存成功！");
        }
      });
    }
  },
  mounted() {}
};
</script>

<style scoped>
.target-dialog-flex {
  display: flex;
}
.target-tree-wrap {
  overflow: auto;
  height: 500px;
  width: 200px;
  padding: 10px 15px;
  margin: 0 5px;
  border: 1px solid #ddd;
}

.target-radio-warp {
  border: 1px solid #ddd;
  height: 500px;
  width: 250px;
  padding: 10px 15px;
  margin: 0 5px;
  overflow: auto;
}
.target-radio-item {
  width: 100%;
  margin: 0;
  padding: 10px 0;
  overflow: hidden;
  text-overflow: ellipsis;
  display: block;
}
.target-calendar-warp {
  flex: 1;
}
</style>



