<template>
  <div>
    <da-manage-page>
      <div slot="button">
        <el-button icon="my-zengjia" @click="enterHandle">手动录入</el-button>
        <el-button icon="my-zengjia" @click="dialogVisible=true;">指标重计算</el-button>
      </div>
      <div slot="content">
        <el-form :inline="true" :model="searchForm.formdata">
          <el-form-item label="所属部门">
            <!-- <el-input v-model="searchForm.formdata.targetId"></el-input> -->
            <da-select-tree :data="orgData" v-model="searchForm.formdata.orgId" :defaultExpandAll="true"></da-select-tree>
          </el-form-item>
          <el-form-item label="指标周期">
            <el-select v-model="searchForm.formdata.targetCycle">
              <el-option label="全部" value="null"></el-option>
              <el-option label="小时指标" value="1"></el-option>
              <el-option label="日指标" value="3"></el-option>
              <el-option label="月指标" value="5"></el-option>
              <el-option label="年指标" value="8"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="指标名称">
            <el-input v-model="searchForm.formdata.targetId"></el-input>
          </el-form-item>

          <el-form-item label="指标类型">
            <!-- <el-input v-model="searchForm.formdata.targetType"></el-input> -->
            <da-select-tree :data="targetTypeData" v-model="searchForm.formdata.targetType" :defaultExpandAll="true"></da-select-tree>
          </el-form-item>

          <el-form-item label="时间">
            <el-select v-model="searchForm.formdata.dataTimeFlag">
              <el-option label="本日" value="3"></el-option>
              <el-option label="本周" value="4"></el-option>
              <el-option label="本月" value="5"></el-option>
              <el-option label="本年" value="8"></el-option>
              <el-option label="自定义" value="0"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="数据时间" prop="dataTime">
            <!-- <el-input v-model="searchForm.formdata.dataTime"></el-input> -->
            <da-time v-model="dataTime" type="daterange" placeholder="选择日期"></da-time>
          </el-form-item>

          <el-form-item label="数据来源">
            <el-select v-model="searchForm.formdata.source">
              <el-option label="全部(较慢)" value="null"></el-option>
              <el-option label="最新记录" value="0"></el-option>
              <el-option label="小时数据" value="1"></el-option>
              <el-option label="日数据" value="3"></el-option>
              <el-option label="月数据" value="5"></el-option>
              <el-option label="年数据" value="8"></el-option>
            </el-select>
          </el-form-item>

          <el-form-item>
            <el-button type="primary" @click="getTbList">查询</el-button>
          </el-form-item>
        </el-form>
        <da-table ref="table" :colums="colsData" :table-list="tableList" :search-data="searchForm" @callBack="getTbList" :showSelection="false">

          <!-- <el-table-column property="operation" label="操作" width="150" slot="operation" align="center" fixed="right">
                    <template slot-scope="scope">
                        <el-button size="small" @click="editTb(scope.row)">编辑</el-button>
                        <el-button size="small" @click="delTb(scope.row)">删除</el-button>
                    </template>
                </el-table-column> -->

        </da-table>
      </div>
    </da-manage-page>
    <manual-enter ref="manualEnterDialog" title="手工录入" :data-list="orgData" :visible.sync=" manualDialogVisible "></manual-enter>
  </div>
</template>
<script>
import { changeTree, difference, deepCopy } from "@/util/util.js";
import manualEnter from "./template/targetDataDialog";
export default {
  name: "targetData",
  components: {
    manualEnter
  },
  data() {
    return {
      orgData: [],
      dataTime: [],
      targetTypeData: [],
      // 查询表单
      searchForm: {
        formdata: {
          orgId: "",
          targetCycle: "",
          targetId: "",
          targetType: "",
          startTime: "",
          endTime: "",
          source: "0"
        },
        currentPage: 1,
        pageSize: 20
      },
      tableList: {}, // 数据列表
      colsData: [
        // 表头
        {
          label: "指标名称",
          prop: "targetName",
          align: "left",
          sortable: true
        },
        { label: "指标数据", prop: "dataValue", align: "left", sortable: true },
        {
          label: "数据时间",
          prop: "dataTime",
          align: "left",
          format: "YYYY-MM-DD HH:mm:ss",
          sortable: true
        },
        {
          label: "最后修改时间",
          prop: "operationTime",
          align: "left",
          format: "YYYY-MM-DD HH:mm:ss",
          sortable: true
        }
      ],
      dialogVisible: false,
      manualDialogVisible: false
    };
  },
  watch: {},
  methods: {
    getTbList() {
      let formName = "table";
      if (this.dataTime && this.dataTime[0]) {
        this.searchForm.formdata.startTime = this.dataTime[0];
        this.searchForm.formdata.endTime = this.dataTime[1];
      }
      this.$ajax
        .get("rest/targetData/page", {
          params: this.searchForm,
          loadingTarget: this.$refs[formName]
        })
        .then(res => {
          if (res.data.status) {
            this.tableList = res.data;
          }
        });
    },
    findOrgData(val) {
      let me = this;
      me
        .$ajax({
          url: "rest/targetData/orgTree",
          method: "get"
        })
        .then(res => {
          if (res.data.status) {
            me.orgData = res.data.records;
            //编辑设值
            if (val != undefined) {
              //没有parentname时
              let parentTreeObj = {};
              changeTree(res.data.records, "id", "name", parentTreeObj);
              // this.$refs.parentModuleTree.setText(parentTreeObj[val.parentId]);
            }
          }
        });
    },
    findTargetTypeTree(val) {
      let me = this;
      me
        .$ajax({
          url: "rest/targetData/targetTypeTree",
          method: "get"
        })
        .then(res => {
          if (res.data.status) {
            me.targetTypeData = res.data.records;
            //编辑设值
            if (val != undefined) {
              //没有parentname时
              let parentTreeObj = {};
              changeTree(res.data.records, "treeCode", "name", parentTreeObj);
              // this.$refs.parentModuleTree.setText(parentTreeObj[val.parentId]);
            }
          }
        });
    },
    enterHandle() {
      this.manualDialogVisible = true;
    }
  },
  mounted() {
    // let dateNow=new Date();
    // let startTime=$
    this.getTbList();
    this.findOrgData();
    this.findTargetTypeTree();
    let arr = [
      { id: "1001", name: "值1", _value: "111" },
      { id: "1001", name: "值1", _value: "11111" },
      { id: "1002", name: "值2", _value: "25462" },
      { id: "1002", name: "值2", _value: "23131" },
      { id: "1002", name: "值2", _value: "2315432" },
      { id: "1003", name: "值3", _value: "333333" }
    ];
    let a1 = { id: "1001", name: "值1", _value: "111" };

    console.log(JSON.stringify(arr).indexOf(JSON.stringify(a1)));
  }
};
</script>



